













































import Vue from "vue";
export default Vue.extend({
  name: "App",
  methods: {
    navigate: function(target: string) {
      window.open(target, "_blank");
    }
  },
  data: () => ({}),
  computed: {
    theme() {
      return this.$vuetify.theme.dark ? "dark" : "light";
    }
  }
});
